* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #22254b;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  background-color: #373b69;
  padding: 2rem;
}

.header h1 {
  font-weight: 400;
}

.header-search {
  width: 250px;
  padding: 0.5rem;
  color: #fff;
  background-color: transparent;
  border: 2px solid #22254b;
  border-radius: 50px;
  font-size: 1rem;
}

.header-search:focus {
  outline: none;
}

.movie-grid {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  padding: 2rem;
}

.movie-item-card {
  width: 250px;
  background-color: #373b69;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.movie-item-card-poster {
  width: 100%;
}

.movie-item-card-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.movie-item-card-title {
  font-size: 1.1rem;
  font-weight: 400;
  margin-right: 0.5rem;
}

.movie-item-card-vote {
  background-color: #22254b;
  padding: 0.5rem;
}

.movie-item-card-vote-green {
  color: green;
}

.movie-item-card-vote-orange {
  color: orange;
}

.movie-item-card-vote-red {
  color: red;
}

.movie-item-card-overview {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  color: #22254b;
  padding: 1rem;
  font-size: 0.9rem;
  line-height: 1.2rem;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
  overflow: auto;
  max-height: 100%;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.movie-item-card:hover .movie-item-card-overview {
  transform: translateY(0%);
}

.movie-item-card-overview h2 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 1200px) {
  .movie-grid {
    width: 900px;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 900px) {
  .movie-grid {
    width: 600px;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 600px) {
  .header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .header h1 {
    margin-bottom: 1rem;
  }

  .movie-grid {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 0;
  }

  .movie-item-card {
    margin-bottom: 2rem;
  }
}
